.items-block__filter {
  display: flex;
  justify-content: flex-end;
  position: relative;
  margin-bottom: 40px;
  @include md {
    margin-bottom: 40px;
    justify-content: flex-end; }
  @include lg {
    margin-bottom: 64px;
    justify-content: center; }

  &-sort {
    display: flex;
    align-items: center;
    position: absolute;
    left: 0;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    color: #000000;
    top: 50%;
    transform: translateY(-50%);
    & .filter-text {
        display: none; }
    @include md {
      .filter-text {
        display: block; } }
    & .sort-btn {
      margin-left: 0;
      & img {
        width: 37px;
        @include md {
          width: 15px; } }
      @include md {
        margin-left: 10px; } } }

  &-wrap {
    display: flex;
    &-title {
      display: flex;
      align-items: center;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;
      color: #6F6E6E;
      margin-left: 28px;
      & a {
        display: flex;
        justify-content: center;
        margin-left: 10px; } } } }

.paginate-block__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 63px;
  @include lg {
    margin-bottom: 96px; }

  .paginate-block__wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #EBEBEB;
    border-radius: 5px;
    & a {
      margin: 0 10px;
      padding-top: 12px;
      padding-bottom: 14px;
      text-decoration: none; }

    & a:first-child {
      font-weight: 300;
      font-size: 16px;
      line-height: 19px;
      color: #EF5B70;
      margin-left: 1rem; }
    & a:not(first-child) {
      font-weight: 300;
      font-size: 16px;
      line-height: 19px;
      color: #C4C4C4; }
    & a:nth-last-child(2) {
      margin-left: 0; }
    & a:nth-last-child(3) {
      margin-right: 0;
      margin-left: 0; }
    & a:nth-last-child(4) {
      margin-right: 0; }
    & a:last-child {
      margin-right: 1rem; } } }
